    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: 'Roboto Condensed', sans-serif;
}
.headline{
  color: Grey;
  font-family: 'Abril Fatface', cursive;
}

.tagline{
  font-family: 'Lobster Two', cursive;
}

.rest{
  font-family: 'Roboto Condensed', sans-serif;
}
*{
    margin: 0;
    padding: 0;
    line-height: 1.5em;
    
}
.descrip{
    color: orange;
    letter-spacing: 0.8px;
}
.newspage-divtag{
    border: 1px grey dotted;
    margin: 20px;
    padding: 20px;
}

a{
    background: yellow;
    -webkit-text-decoration: wavy;
            text-decoration: wavy;
    font: italic;
    font-size: 0.9em;
}
.headline{
    color: white;
    font-family: 'Abril Fatface', cursive;
  }
  
h1{
    color: black;
    font-family: 'Lobster Two', cursive;
    font-weight: bolder;
    font-size: 3.5em;
    
}

h2{
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 2em;
}
h6{
    font-weight: lighter;
}
#detailsbutton{
    margin: 12px 0px;
    background: orange;
    border: transparent;
    border: 1px black solid;
    padding: 5px;   
}
#detailsbutton:hover{
    background: lightgrey;
    color: white;
    border: 2px solid lightgrey;
}
#modalclosebutton{
    
    position: -webkit-sticky;
    
    position: sticky;
    left: 98%;
    background: red;
    border: transparent;
    border-top: 2px black solid;
    border-bottom: 2px black solid;
    padding: 2px;
}
#modalclosebutton:hover{
    background: red;
    color: white;
    border-color: red;
    transition-duration: 0.2s;
}
img{
    margin-top: 20px;
    margin-bottom: 20px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: auto;
    border: 2px solid darkgray;
}
main {
    display: flex;
   
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
  }
a{
    text-decoration: none;
    color: yellow;
}
main a{
    cursor: pointer;
    position: relative;
    display: inline-block;
    -webkit-text-decoration: wavy;
            text-decoration: wavy;
    text-decoration: none;
    font-size: 1em;
    background-clip: text;
    -webkit-background-clip: text;
    color: yellow;
    background-repeat: no-repeat;
    transition: background 0.2s ease-out;
    white-space: nowrap;
}
  
span {
position: relative;
}

span:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    height: 16px;
    width: 0.1px;
    background: yellow;
    bottom: 4px;
    text-decoration: none;
    transition: all 1s ease-out;
}

a:hover{
    color: yellow;
    text-decoration: none;
}

span:hover:before{
    transform: translateX(2px);
    width: 198px;
    background-color: lightsalmon;
}


*{
  background: white;
  margin: 0;
  padding: 0;
  color: black;
}
#newicon{
  width: 120px;
  height: 100px;
  margin-left: 5%;
}

.headline{
  font-family: 'Abril Fatface', cursive;
  color: white;
  text-transform: uppercase;
  background: linear-gradient(orange, salmon);;
  font-weight: 900;
  font-size: 50px;
  
  padding: 20px;
  text-align: center;
  letter-spacing: 0.2em;
}
.headiconandtitle{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  background: linear-gradient(orange, salmon);
  border: 2px grey solid;
  border-bottom: 5px grey solid;
}
#emailsubs{
  width: 50%;
  position: -webkit-sticky;
  position: sticky;
  border-color: lightsalmon;
  border-style: solid;
  border-width: 20px 5px;
  margin: 10% 25%;
  padding: 20px;
  border-radius: 8px;
}

#emailandclose{
  display: flex;
  flex-direction: row;
  justify-content: left;
  font-family: 'Roboto Condensed', sans-serif;
}

.containerwa{
  background-image: linear-gradient(to right, rgba(255,0,0,0), rgba(255,165,0,0.5));
}
#subtoget{
  font-family: 'Lobster Two', cursive;
  font-size: 2em;
  color: lightsalmon;
}

#closebutton{
  display: inline;
  position: -webkit-sticky;
  position: sticky;
  left: 90%;
  height: 20%;
  width: 6%;
  background: orange;
  border: transparent;

  border-left: 2px black solid;
  border-right: 2px black solid;
  
}
#closebutton:hover{
  background: red;
  color: white;
  transition-duration: 0.2s;
  border: 2px red solid;
}
#Subscribe{
  margin-top: 5px;
}
#Subscribe:hover{
  background: lightsalmon;
  color: black;
  transition-duration: 0.2s;
  border-color: lightsalmon;
}
